import { Injectable } from "@angular/core";
import { Http, Headers, RequestOptions, Response } from "@angular/http";
import { Observable } from "rxjs/Rx";
import "rxjs/add/operator/do";
import "rxjs/add/operator/catch";
import "rxjs/add/operator/map";
import { environment } from "../environments/environment";
import { SigninService } from "./signin.service";

@Injectable()
export class LookupService {
    private _locaties: string[] = [];

    constructor(private _http: Http, private _signinService: SigninService) {
        //
    }

    getLocaties(): Observable<string[]> {

        return this._http.get(`${environment.serviceUrl}/api/lookup/getlocaties`, this.createRequestOptions())
            .map((response: Response) => response.json() as string[])
            .catch(this.handleError);
    }

    getKentekens(): Observable<string[]> {
        return this._http.get(`${environment.serviceUrl}/api/lookup/getkentekens`, this.createRequestOptions())
            .map((response: Response) => response.json() as string[])
            .catch(this.handleError);
    }

    private handleError(error: Response) {
        console.error(error);
        return Observable.throw(error.json().error || "Server error");
    }

    private createRequestOptions(): RequestOptions {
        const headers = new Headers();
        headers.append("UserAuthToken", this._signinService.userAuthToken);
        return new RequestOptions({ headers: headers });
    }
}


