import { Component, NgZone } from "@angular/core";
import { environment } from "../../environments/environment";
import { SigninService } from "../signin.service";
import { BaseComponent } from "../base.component";

declare var gapi: any;

@Component({
    selector: "app-signin",
    templateUrl: "./signin.component.html",
    styleUrls: ["./signin.component.css"]
})
export class SigninComponent extends BaseComponent {
    googleLoginButtonId = "google-login-button";

    constructor(private _zone: NgZone, signinService: SigninService) {
        super(signinService);
    }

    ngAfterViewInit() {
        gapi.signin2.render(
            this.googleLoginButtonId,
            {
                "onSuccess": this.onGoogleLoginSuccess,
                "scope": "profile",
                "theme": "dark"
            });
    }

    onGoogleLoginSuccess = (loggedInUser) => {
        this._zone.run(() => {
            this.signinService.userAuthToken = loggedInUser.getAuthResponse().id_token;
            this.signinService.userDisplayName = loggedInUser.getBasicProfile().getName();
            this.signinService.isAuthenticated = true;
            //console.log(`userAuthToken: ${this.signinService.userAuthToken}`);
            //console.log(`userDisplayName: ${this.signinService.userDisplayName}`);
        });
    };
}


