import {Injectable} from "@angular/core";
import {Rit} from "./rit";
import {Http, Headers, RequestOptions, Response} from "@angular/http";
import {Observable} from "rxjs/Rx";
import "rxjs/add/operator/do";
import "rxjs/add/operator/catch";
import "rxjs/add/operator/map";
import { environment } from "../environments/environment";
import { SigninService } from "./signin.service";

@Injectable()
export class RitService {
    private _ritten: Rit[] = [];
    private _index = 0;

    constructor(private _http: Http, private _signinService: SigninService) {
        //
    }

    updateRit(rit: Rit): Observable<Rit> {
        return this._http.post(`${environment.serviceUrl}/api/rit/update`, rit, this.createRequestOptions(true))
            .map((response: Response) => response.json() as Rit)
            .catch(this.handleError);
    }

    getAllRitten(): Observable<Rit[]> {
        return this._http.get(`${environment.serviceUrl}/api/rit/getall`, this.createRequestOptions())
            .map((response: Response) => response.json() as Rit[])
            .catch(this.handleError);
    }

    getRitById(id: number): Observable<Rit> {
        return this._http.get(`${environment.serviceUrl}/api/rit/getbyid/${id}`, this.createRequestOptions())
            .map((response: Response) => response.json() as Rit)
            .catch(this.handleError);
    }

    private handleError(error: Response) {
        console.error(error);
        return Observable.throw(error.json().error || "Server error");
    }

    private createRequestOptions(withContentType: boolean = false): RequestOptions {
        const headers = new Headers();
        headers.append("UserAuthToken", this._signinService.userAuthToken);
        if (withContentType) {
            headers.append("Content-Type", "application/json");
        }

        return new RequestOptions({ headers: headers });
    }
}


