import {Component, OnInit, OnDestroy} from "@angular/core";
import {Rit} from "../rit";
import {RitService} from "../rit.service";
import { BaseComponent } from "../base.component";
import { SigninService } from "../signin.service";
import {Subscription} from "rxjs";

@Component({
    selector: "app-rit-list",
    templateUrl: "./rit-app-list.component.html",
    styleUrls: ["./rit-app-list.component.css"]
})
export class RitAppListComponent extends BaseComponent implements OnInit, OnDestroy {
    private _ritten: Rit[] = [];
    private _subscription: Subscription;
    isRequesting = false;

    constructor(private _ritService: RitService, signinService: SigninService) {
        super(signinService);
    }

    ngOnInit(): void {
        this._subscription = this.signinService.isAuthenticated$.subscribe(isAuthenticated => {
            if (isAuthenticated) {
                this.isRequesting = true;
                this._ritService.getAllRitten()
                    .subscribe((ritten) => {
                            this._ritten = ritten;
                            this.isRequesting = false;
                        },
                        error => this.handleError(error));
            }
        });
    }

    ngOnDestroy(): void {
        this._subscription.unsubscribe();
    }

    get ritten(): Rit[] {
        return this._ritten;
    }

    private handleError(error: string) {
        this.isRequesting = false;
        console.error(error);
        alert(error);
    }
}


