import { Injectable, isDevMode } from "@angular/core";
import {BehaviorSubject} from "rxjs/BehaviorSubject";

@Injectable()
export class SigninService {

    private _isAuthenticated = new BehaviorSubject<boolean>(false); // false is your initial value
    isAuthenticated$ = this._isAuthenticated.asObservable();
    private _userAuthToken: string = null;
    private _userDisplayName: string = null;

    get isAuthenticated(): boolean {
        return isDevMode() ? true : this._isAuthenticated.getValue();
    }
    set isAuthenticated(value: boolean) {
        this._isAuthenticated.next(value);
    }

    get userAuthToken(): string {
        if (isDevMode()) {
            return "eyJhbGciOiJSUzI1NiIsImtpZCI6IjhmYTE3YTc1OTk4MTU1ZDU3MDJiMzcxNWY1NTljNTEzYmI4MWJkMWIifQ..A6jEXBNovklKgSoqXUleQ8QUdyAf-_Y877ZfLNot291o0b-nXM3RULF_lqUNlNUTR3utInGCPqt7zrI7MXk7wJ38bCXFGtG-8oJS7FdZwp-z0KA9Q3JLrk7fHVIKL5KbIOCYqOHwpZvqemgg2SUVlSKz1TnC2pa27MfEA7ygPHVrYGl22HdauMkRU5UgyYEX3uVJowvnkoBy1pNK7WhFNSue5QewgSKrHD0VT0hL8zmuN2wk_OAe0dz_cslHVHX2Har0LHPudnlDg3O2nS6qLuazCIF4fEvfRjVcIKmN0UnNWMbkUf1Mqrv6TI7pqq5-t3m92t1_4jlqyWXWdiDYZQ";
        }
        return this._userAuthToken;
    }
    set userAuthToken(value: string) {
        this._userAuthToken = value;
    }
    
    get userDisplayName(): string {
        return isDevMode() ? "John Doe" : this._userDisplayName;
    }
    set userDisplayName(value: string) {
        this._userDisplayName = value;
    }
}


